<template>
  <v-container grid-list-md fluid>
        <v-layout row wrap>
          <!-- <v-flex xs12 sm6 offset-sm3> -->
          
          <v-flex v-for="(card, index) in cards" :key="index" xs4>
            <!-- <v-slide-y-transition mode="out-in"> -->
            <v-card tile>
              <v-img
                class="white--text"
                height="200px"
                :src="card.src"
              >
                <v-container fill-height fluid>
                  <v-layout fill-height>
                    <v-flex xs12 align-end flexbox>
                      <span class="headline">{{card.title}}</span>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-img>
              <v-card-title>
                <div>
                  <span class="grey--text">{{index}}</span><br>
                  <!-- <span>Whitehaven Beach</span><br>
                  <span>Whitsunday Island, Whitsunday Islands</span> -->
                </div>
              </v-card-title>
              <v-card-actions>
                <v-btn flat color="orange">Share</v-btn>
                <v-btn flat color="orange">Explore</v-btn>
              </v-card-actions>
            </v-card>
            <!-- </v-slide-y-transition> -->
          </v-flex>
          
        </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'ImageDashBoard',
  props:{
    count:{
      type:Number,
      default:10
    }
  },
  data() {
    return {
      cards:[]
    }
  },
  mounted(){
    this.refresh()
  },
  methods: {
    refresh:function(){
      this.cards = [];
      const start = parseInt (Math.random() * 100);
      for (let index = 0; index < this.count; index++) {
        this.cards.push({
          title:"image"+index,
          src:"https://picsum.photos/200/250/?image="+ (start + index)
        })
      }
      
    }
  }
}
</script>

